
.detail {
  min-height: 100%;
  height: auto;
  position: relative;
}
.detail .works {
    padding: 0 2rem 2rem;
}
.detail .works ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-flow: row wrap;
              flex-flow: row wrap;
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
}
.detail .works ul li {
        margin: 0 .5rem;
}
.detail .works ul img {
        width: 10rem;
        height: 14rem;
        margin-top: 2rem;
}
.detail .works ul p {
        margin: .1rem 0;
}
.detail .works .role span {
      display: inline-block;
      margin-right: .5rem;
      color: #ababab;
}



