
.main {
  padding-top: 3rem;
  padding-bottom: 4rem;
  background: #ddd;
}
.main .wrapper {
    margin-top: 1rem;
}
.fade-enter-active,
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.blue {
  color: #24BDFF;
}
.mt30 {
  margin-top: 3rem;
}
.introduction {
  padding: 2rem;
}
.introduction .title {
    color: #ababab;
    font-size: 1.2rem;
    font-weight: 400;
}
.introduction .content {
    font-size: 1.2rem;
    line-height: 2rem;
    text-indent: 2em;
    margin-top: 1rem;
    height: 10rem;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
}
.introduction .more {
    float: right;
    color: #24BDFF;
}



