}
.fade-enter-active,
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.blue {
  color: #24BDFF;
}
.mt30 {
  margin-top: 3rem;
}
.van-fixnav {
  position: fixed;
  left: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 4rem;
  border-top: 1px solid #ddd;
  background: #fff;
  z-index: 100;
}
.van-fixnav a {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    color: #666;
}
.van-fixnav a p {
      margin-top: .2rem;
}
