    margin: .5rem 0;
    color: #ababab;
}
.detail-title .rating-box {
    position: absolute;
    width: 8rem;
    height: 8rem;
    top: 2rem;
    right: 2rem;
    box-shadow: 0 0 1rem #ddd;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.detail-title .rating-box .title {
      color: #ccc;
}
.detail-title .rating-box .rating {
      font-size: 2rem;
}
.detail-title .rating-box .person {
      color: #ababab;
}
.detail-title .rating-box p {
      margin: .1rem 0;
}



