
.main {
  padding-top: 3rem;
  padding-bottom: 4rem;
  background: #ddd;
}
.main .wrapper {
    margin-top: 1rem;
}
.fade-enter-active,
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.blue {
  color: #24BDFF;
}
.mt30 {
  margin-top: 3rem;
}
.more-list {
  padding: 2rem 4rem 2rem 1rem;
  border-bottom: 1px solid #ddd;
}
.more-list figure {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.more-list .content {
    margin-left: 1.5rem;
    padding-top: 1rem;
}
.more-list .content p {
      font-size: 1.2rem;
      margin: .5rem 0;
}



