
.main {
  padding-top: 3rem;
  padding-bottom: 4rem;
  background: #ddd;
}
.main .wrapper {
    margin-top: 1rem;
}
.fade-enter-active,
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.blue {
  color: #24BDFF;
}
.mt30 {
  margin-top: 3rem;
}
.show-container {
  padding: 1rem 0 2rem 0;
  background: #fff;
}
.show-container .header {
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
    margin-bottom: .5rem;
}
.show-container .header h2 {
      float: left;
      font-size: 1.4rem;
}
.show-container .header a {
      float: right;
      color: #24BDFF;
      line-height: 3rem;
}
.show-container .swiper-container img {
    width: 10rem;
    height: 14rem;
}
.show-container .swiper-container .swiper-wrapper {
    padding: 0 1rem;
}
.show-container .swiper-container .title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: .5rem 0;
}



