
.topbar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  border-bottom: 1px solid #ddd;
  background: #fff;
  z-index: 100;
}
.topbar p {
    height: 3rem;
    line-height: 3rem;
    text-align: center;
}
.topbar .back {
    position: absolute;
    top: 0;
    left: 1rem;
    line-height: 3rem;
}



